import { IContainerModel } from "../quick/IContainerModel";
import { ISettings } from "../quick/IPlateauUI";

/**
 * @see https://wbd.ms/share/v2/aHR0cHM6Ly93aGl0ZWJvYXJkLm1pY3Jvc29mdC5jb20vYXBpL3YxLjAvd2hpdGVib2FyZHMvcmVkZWVtLzUxMmU1ZTBmOWIyYTRjMzJhMGRlNjMwY2UzNThhNTRhX0JCQTcxNzYyLTEyRTAtNDJFMS1CMzI0LTVCMTMxRjQyNEUzRA==
 */
export type IWSRSMessage = {
    verifyWssrRequest: { // source | target -> wsrs
        type: "verifyWssrRequest",
    },
    verifyWssrResponse: { // wsrs -> source | target
        type: "verifyWssrResponse",
    },
    clientIDRequest: { // source | (target) -> wsrs 
        type: "clientIDRequest",
    },
    clientIDResponse: { // wsrs -> source | (target)
        type: "clientIDResponse",
        clientID: string,
    },
    error: {
        type: "error",
        error: string,
        screenText?: string
    },
    settingsQJsonRequest: { // source -> (wsrs) -> target(s)
        type: "settingsQJsonRequest",
        pipelineQjson?: string,
        alertQjson?: string
    },
    relayRequest: { // target -> wsrs -> source
        type: "relayRequest",
        clientID: string,
        senderName: string, // target fills
        senderID: string, // wsrs fills -> source
    },
    relayResponse: { //source -> (wsrs) -> target(s)
        type: "relayResponse"
    },
    relayStopRequest: { // source -> wsrs -> target | target(s)  ||  target -> wsrs -> source
        type: "relayStopRequest",
        clientID?: string, // source fills or leaves if source initiates || wsrs fills if target initiates
    },
    displayQJsonRequest: { // source -> (wsrs) -> target(s)
        type: "displayQJsonRequest",
        qjson: string,
    },
    clientLogRequest: { // target -> wsrs -> source
        type: "clientLogRequest",
        log: Array<{ source: "native" | "engine" | "model", level: "log" | "error" | "warning" | "debug", items: Array<any> }>,
    },
    pingRequest: { // wsrs -> source | target
        type: "pingRequest"
    },
    pongResponse: { // source | target -> wsrs
        type: "pongResponse"
    },
    editModeChangeRequest: { //source -> (wsrs) -> target(s)
        type: "editModeChangeRequest",
        isEditMode: boolean
    },
    getQJsonRequest: { // target -> wsrs -> source
        type: "getQJsonRequest",
        qJsonPath: string
    },
    getQJsonResponse: { //source -> (wsrs) -> target(s)
        type: "getQJsonResponse",
        qJson?: string
    },
    managePingRequest: { // target -> wsrs
        type: "managePingRequest",
        disablePing: boolean
    },
    networkRequestRequest: { // target -> wsrs -> source
        type: "networkRequestRequest"
        method: | 'get' | 'delete' | 'head' | 'options' | 'post' | 'put' | 'patch' | 'purge' | 'link' | 'unlink'
        | 'GET' | 'DELETE' | 'HEAD' | 'OPTIONS' | 'POST' | 'PUT' | 'PATCH' | 'PURGE' | 'LINK' | 'UNLINK',
        body?: any,
        headers?: Record<string, string>,
        url: string,
    },
    networkRequestResponse: { // source -> wsrs -> target
        type: "networkRequestResponse"
        body?: any,
        status: number,
        statusText: string,
        headers?: Record<string, any>,
    },
    getStyleRequest: { // source -> (wsrs) -> target(s)
        type: "getStyleRequest",
        style?: string,
        theme?: object
    },
    getSettingsRequest: {
        type: "getSettingsRequest",
        platform: "web" | "mobile"
    },
    getSettingsResponse: {
        type: "getSettingsResponse",
        settings: ISettings
    },
    getResourceRequest: {
        type: "getResourceRequest";
        path: string | Array<string>;  // <<qjson:abjchasvdk>> <<woff:abjchasvdk>> <<image:abjchasvdk>> ...
    },
    getResourceResponse: {
        type: "getResourceResponse";
        content: string | Array<string>;  // <<qjson:abjchasvdk>> <<woff:abjchasvdk>> <<image:abjchasvdk>> ...
        error?: true | Array<number>;
    },
    fontNameRequest: {
        type: "fontNameRequest";
        combinedFont: string,
    },
    fontNameResponse: {
        type: "fontNameResponse";
        content: string;  
    },
    containerConfigurationRequest:{
        type: "containerConfigurationRequest";
        containerConfiguration?: string;
    }

};